import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute} from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';
import { AccessProviders } from '../../providers/access-providers';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';

///SEMNA ALTI
import { semana_A } from '../modelo_A/semana_A';


//registro Llanos
import { semana_L } from '../modelo_L/modelo_L';

//registro semana VAlle 
import { semana } from '../modelo/semana';


@Component({
  selector: 'app-intro',
  templateUrl: './intro.page.html',
  styleUrls: ['./intro.page.scss'],
})
export class IntroPage implements OnInit {



  //SEMANA A
  objeto:semana_A = {numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
  
   semanas: semana_A[] = [];
   url2 = 'http://localhost/proG/server_api/menu_semanal_A.php';


  //SEMANA lLANOS
  objeto1:semana_L = {numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
   
  semanas1: semana_L[] = [];
  url3 = 'http://localhost/proG/server_api/menu_semanal_L.php';


  //SEMANA VALLE

  objeto2:semana = {numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
  
  semanas2: semana[] = [];
  url4 = 'http://localhost/proG/server_api/menu_semanalv.php';
  

    

  constructor(
    public http: HttpClient,
    private toastCtrl: ToastController,
    private accsPrvds: AccessProviders
    ) {
      this.listarSemanas();
      this.listarSemanas1();
      this.listarSemanas2();
  }

  ngOnInit() {
  }

  //SEMANA A

  getSemanas(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarSemanas(){
    this.semanas = [];
    this.getSemanas().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas.push(res[i]);
      }
    })
  }

  //SEMANA LLANOS

  getSemanas1(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarSemanas1(){
    this.semanas1 = [];
    this.getSemanas1().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas1.push(res[i]);
      }
    })
  }

  //SEMANA VALLLE

  getSemanas2(): Observable<any>{
    return this.http.get(this.url4);
  }

  listarSemanas2(){
    this.semanas = [];
    this.getSemanas2().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas2.push(res[i]);
      }
    })
  }


 

}
